@font-face {
  font-family: themeFont;
  src: url('Legacy.otf');
}

@font-face {
  font-family: headerFont;
  src: url('FFFFORWA.TTF');
}

@font-face {
  font-family: postTitleFont;
  src: url('Cascadia.ttf');
}

@font-face {
  font-family: postContentFont;
  src: url('ptsans/PTS55F.ttf');
}


* {
  box-sizing: border-box;
}

html {
    margin: 0px;
    padding: 0px;
}

body {
 	margin: 0px;
    padding: 0px;
	font-family: postTitleFont;
	background-color: #000000;
	background-repeat: no-repeat;
	color: #dedede
}

.navbar {
	overflow: auto;
	height: 44px;
	width: 100%;
	box-sizing:border-box;
	box-shadow: 0px 0px 8px 1px #222222;
	background-color: #777777;
}

.current {
 	color: rgb(70,80,70);

}

.uncurrent {
	color: rgb(240,240,240);
}

#navlink {
	float: right;
	margin-top: 10px;
	margin-right: 10px;
	font-family: headerFont;
}

h1 {
	margin: auto;
	color: #DEE2EC;
}

.postTitle {
		font-family: postTitleFont;
		color: #a03a2b
}

.postContent {
		font-family: postContentFont;
}

.headerbar {
	height: 64px;
	width: 100%;
	box-sizing:border-box;
	overflow: hidden;
	color: #DEE2EC;
	text-shadow: 1px 1px #dc5873;
	font-family: headerFont;
}

.leftspacedtitle {
	margin-top: 0px;
	margin-bottom: 10px;
   	padding: 10px;
   	font-family: headerFont;
}

.separator-basic {
	height: 2px;
	width: 100%;
	margin: 0px;
	padding: 0px;
	background-color: #777777;
}

a {
	color: #a05b3c;
	text-decoration: none;
}

.col-1 {width: 8.33%;}
.col-2 {width: 16.66%;}
.col-3 {width: 25%;}
.col-4 {width: 33.33%;}
.col-5 {width: 41.66%;}
.col-6 {width: 50%;}
.col-7 {width: 58.33%;}
.col-8 {width: 66.66%;}
.col-9 {width: 75%;}
.col-10 {width: 83.33%;}
.col-11 {width: 91.66%;}
.col-12 {width: 100%;}

[class*="col-"] {
  float: left;
  padding: 15px;
  margin: 5px;
  overflow: hidden;
  border: 1px solid #999999;
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  [class*="col-"] {
    width: 100%;
  }
}

.savedextra {
	  
	  background-color: #63a583;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}

.sidebarcat {
	background-color: #999999;
	padding: 15px;
}

.maincontent {
	box-shadow: 3px 6px #999999;
}

footer {
  text-align: center;
  margin-top: 50px;
  padding: 3px;
  background-color: #777777;
  color: white;
}